body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.pressDown {
  background-color: rgb(153, 94, 209);
  padding: 10px 30px;
  color: #fff;
  border-radius: 5px;
  border-bottom: 4px solid rgb(153, 94, 209);
  transition: 0.1s;
  cursor: pointer;
}
.buttonLeft {
  height: 45px;
  width: 120px;
  padding: 0 14px;
  overflow: hidden;
  background:"blue";
  position: relative;
  line-height: 40px;
  color: #000000;
  text-align: center;
  border-radius:10px;
  border-width:2px;
}

.buttonLeft::before {
  content: 'GET CV';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  background: #071a52;
  transition: transform .2s ease-in-out;
  color: #fff;
}

.buttonLeft:hover::before {
  transform: translateX(0);
}

.buttonLeft span {
  position: relative;
  z-index: 1;
}

